import React, { PureComponent } from "react";
import Masonry from "react-masonry-component";
import Card from "../../Elements/Card";
import ModernAnimation from "../../Elements/AnimateComp/ModernAnimation";
import "../common.scss";
import { withData } from "../../Elements/Observer";
import { getPreview } from "../../../../utils";
class ModernTheme extends PureComponent {
  responsiveUpdate = null;
  heightMange = null;

  constructor(props) {
    super(props);
    this.state = {
      postWidth: 0,
      checkThemeheight: props.wall.isDisplay || props.wall.product ? false : true,
      productUpdate : 0
    };
  }

  componentDidMount() {
    this.responsiveUpdate = setTimeout(() => this.onResponsiveTheme(), 1000);
    window.addEventListener("resize", () => {
      if(this.props.isDisplay) this.themeheight();
      this.onResponsiveTheme();
    });

    /* ==============FOR MOUSE SCROLL STOP============== */
    if (this.props.wall.isDisplay  || this.props.wall.product) this.themeheight(1)
  }

  themeheight = (status = 0) => {
    const { heightUpdate, wall } = this.props;

      if (wall.isDisplay  || wall.product) {
        if (wall.isDisplay) heightUpdate();
        this.heightMange = setTimeout(() => this.setState({ checkThemeheight: true }), 100)
      }
  };

  onResponsiveTheme = () => {
    const { wall } = this.props;
    const tb_app_container = document.querySelector(".tb_app_container");

    if (tb_app_container) {

      const rootWidth = tb_app_container.clientWidth - (!wall.isDisplay ? 8 : 1);

      if (rootWidth > 0) {
        let cardNumber = null;
        let adjustSpacing = null;
        let spacing = null;
        let adjustWidth = null;
        const postWidth = wall.Personalization.minimumPostWidth;
        cardNumber = rootWidth / postWidth;
        let rowCard = Math.trunc(cardNumber);
        spacing = rootWidth % postWidth;
        adjustSpacing = spacing / rowCard;
        if (cardNumber >= 1) adjustWidth = postWidth + adjustSpacing;
        else adjustWidth = postWidth - adjustSpacing;
        this.setState({ postWidth: adjustWidth });
      }
    }
  };

  componentDidUpdate(pre, prevState) {

    const { wall } = this.props;
    const { isDisplay, isEditor, Personalization, Banner, BannerImage } = wall

      const preview =  getPreview()

      const heightcheck = Banner.bannerUpdate!= pre.wall.Banner.bannerUpdate || Banner.banner_position!=pre.wall.Banner.banner_position || BannerImage.status!=pre.wall.BannerImage.status;

        if(wall.product!=pre.wall.product) this.setState({ productUpdate : Math.random() })
        if(wall.product) this.responsiveUpdate = setTimeout(() => this.onResponsiveTheme(), 2000)

        if ((isEditor && isDisplay) && (preview || Personalization.minimumPostWidth!=pre.wall.Personalization.minimumPostWidth || this.props.themeUpdate  || heightcheck )) {
               this.responsiveUpdate = setTimeout(() => {
                 this.onResponsiveTheme()
                 this.themeheight() 
              }, 2000)
         }
  }
  

  componentWillUnmount() {
    if (this.responsiveUpdate) clearTimeout(this.responsiveUpdate);
    if (this.heightMange) clearTimeout(this.heightMange);
    window.removeEventListener("resize", this.themeheight);
  }

  render() {
    let { checkThemeheight, postWidth, productUpdate } = this.state;

    let {
      postData,
      completeDataObject,
      wall,
      clickToShowPopUp,
      themeHeight, intractiveData
    } = this.props;

    let { ThemeRule } = wall;
    const { themeId } = ThemeRule;

    const cardStyle = {
      backgroundColor: ThemeRule.cardColor,
      padding: `${ThemeRule.spacing}px`,
      borderRadius: `${ThemeRule.borderRadius}px`,
    };
    return (
      <>
        {checkThemeheight ? (
          <ModernAnimation key={productUpdate}  {...this.props}>
            <div
              id={
                !wall.isDisplay
                  ? `${
                      themeId == 62
                        ? "casecade"
                        : themeId == 63
                        ? "focus"
                        : themeId == 64
                        ? "exhibit"
                        : "pixel"
                    }_${themeId}`
                  : null
              }
              className={`${themeId == 66 ? `themeSquare` : ''} themeModrnWatrFall ${wall.isDisplay ? `display` : "widget"} ${themeId==78 ? `roundeageTheme` : ''} waterfall-theme` }>

               <div id="marqueecontainer" style={{ overflow: wall.isDisplay ? "auto" : "inherit", height: wall.isDisplay ? `calc(100vh - ${themeHeight+4}px)` : 'auto' }}>    
                <Masonry
                  id="wPosts"
                  className={`theme${themeId}`}
                  tabIndex="0"
                  aria-label={`There are ${
                    postData && postData.length > 0 ? postData.length : 0
                  } posts in the feed`}
                  elementType={"div"}
                  options={{
                    percentPosition: true,
                    resize: true,
                  }}
                  disableImagesLoaded={false}
                  updateOnEachImageLoad={true}
                  enableResizableChildren={true}
                >
                  {postData &&
                    postData.map((item, itemKey) => {
                      const itemData = completeDataObject[item];
                      const HocCard = wall.displayEmbed ? Card : withData(Card);
                      let isCardActive = false;
                      if(postWidth && itemData && Object.keys(itemData).length > 0){
                        if(themeId == 66 && itemData.postFileNew) isCardActive = true;
                        else if(themeId != 66 && (itemData.postFileNew || !wall.ThemeRule.hideContent || itemData.rating)) isCardActive = true;
                      }

                      return isCardActive ? (
                        <div
                          key={itemKey}
                          id={`"postId${itemData.id}`}
                          className={`feedId${
                            itemData.feedId
                          } postItem item flatCard   ${
                            themeId == 65 ? `pixel65` : ""
                          }  ${itemData.network.name}Taggbox ${
                            itemData.theme48 &&
                            itemData.theme48.class1 === "onlyTextCard"
                              ? "onlyTextCard"
                              : ""
                          } completeFadeIn`}
                          data-post-id={itemData.id}
                          data-highlight={itemData.highlight}
                          data-pin={itemData.pin}
                          style={{
                            padding: ThemeRule.padding / 2,
                            width: itemData.highlight ? postWidth : postWidth,
                            margin: 0,
                            minWidth: 0,
                            left: 0,
                            top: 0,
                          }}
                          data-created={itemData.createdAt}
                        >
                          <div className={`post`} style={cardStyle}>
                            <div className="postContent">
                              <HocCard
                                wall={wall}
                                itemData={itemData}
                                key={itemKey}
                                itemIndex={itemKey}
                                clickToShowPopUp={clickToShowPopUp}
                                intractiveData={intractiveData}
                              />
                            </div>
                          </div>
                        </div>
                      ) : null;
                    })}
                </Masonry>
              </div>
            </div>
          </ModernAnimation>
        ) : null}
      </>
    );
  }
}  
export default ModernTheme;