import React, { PureComponent } from "react";
import { connect } from "react-redux";
import AuthorDetails from "./AuthorDetails";
import Media from "../Elements/Media";
import PostContent from './PostContent/PostContentDisplay'
import Rating from "./Rating/Rating";
import Network from "./Network/Network";
import InteractiveEmoji from "./InteractiveEmoji";
class Card extends PureComponent {

  
  render() {
    const { itemData, clickToShowPopUp, itemIndex, wall, autoplay, cardRef, intractiveData } = this.props;
    const { Personalization, ThemeRule } = wall;
    const mediaType = [2, 3, 4, 5].includes(itemData.type);
    const postAuthor = (ThemeRule.postAuthor && !itemData.author.isInstaUser) ? true : false;
    const network = itemData.network;
    const interactive = wall.Personalization.interactive
    return (
      <div className={`tb_mwf_post_wrapper ${interactive ? 'intractive_on' : ''}`} data-id={itemData.id} tb-network={network.icon} ref={cardRef}>
        <div className="tb_mwf_post_in" onClick={clickToShowPopUp(itemIndex, itemData)}>
          <div className="tb_mwf_post_media_wrapp">
            {!mediaType && ThemeRule.themeId != 65 ? <div className="tb_mwf_social_top_"><Network networkClass={'tb_mwf_social_center_ico'} network={network} ThemeRule={ThemeRule} /> </div> : null}

            {mediaType ? <Media itemData={itemData} wall={wall} autoplayVideo={autoplay} />  : null}

            {itemData.rating ? <div className={`tb_mwf_rating__ ${network.id === 29 ? `tb_mwf_onsite_rating__` : ``}`}>
              <Rating rating={itemData.rating} network={network} /></div> : null}
          </div>

        {!ThemeRule.captionStatus ?  
          <div className={ThemeRule.postAuthor || ThemeRule.socialAction || ThemeRule.postTime ? `tb_mwf_contant_wrapper` : ''}>
            { (postAuthor || ThemeRule.postTime || (network.id && ThemeRule.socialAction) ) ? <AuthorDetails postData={itemData} ThemeRule={ThemeRule} /> : null }
            { !ThemeRule.hideContent && ![65,63].includes(ThemeRule.themeId) ? <PostContent key={itemData.id} item={itemData}  ThemeRule={ThemeRule}  personalization={Personalization} contentTitle={itemData.contentTitle} wall={wall}  /> : null}
          </div> : null}
          {
           !wall.isDisplay && interactive ?
            <InteractiveEmoji {...{isCard:true, postId:itemData.id, intractiveData, userDetail: wall.UserDetail, wallId: wall.Wall, interactiveCount: itemData.interactiveCount, isDisplay: wall.isDisplay, interactive: interactive, interactiveStatus : wall.Personalization.interactiveStatus}} key={(Math.random() * itemData.id)}/> : null
          }
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    modalPop: state.modalPop
  }
}

export default connect(mapStateToProps)(Card);