import React from "react";
import { isOldBrowser } from "../../../utils";

export function withData(WrappedComponent) {
    return class extends React.Component {
        cardRef = React.createRef(null);
        constructor(props) {
            super(props);
            this.state = { autoplay : false };
        }
        

      startLoadingVideo = (ref) => {
        const { itemData, wall } = this.props;

        if(wall && wall.ThemeRule && Object.keys(wall.ThemeRule).length > 0 && itemData.type == 3 || itemData.type == 5){
          const themeId = wall.ThemeRule.themeId;
          const newBrower = !isOldBrowser();
          try {
            if(newBrower && window.innerWidth > 920 && themeId && [62, 63, 64, 65, 66, 69, 70, 73].includes(parseInt(themeId))){
              let observer = new IntersectionObserver(entries => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) this.setState({ autoplay: true }); 
                    else this.setState({ autoplay: false });
                });
              },{ rootMargin: "50px", threshold: 1.0 });
              if (ref.current) observer.observe(ref.current);
      
            }else this.setState({ autoplay: true });
          } catch (error) {
            this.setState({ autoplay: true });
          }
        }
      }
    
    componentDidMount(){
       this.startLoadingVideo(this.cardRef);
     }
      
      render() {
        // Pass the data to the wrapped component
        return <WrappedComponent {...this.state} {...this.props} cardRef={this.cardRef} />;
      }
    };
  }