import { PureComponent,memo} from "react"; 

var ReachedMaxScroll = false;
var PreviousScrollTop = 0;
var setTimescroll = 0;
var ScrollInterval=0;
var scrollTime, DivElmnt;


const speedUpPixel = scrollDelay => ({
  80: 60,
  70: 40,
  50: 30,
  30: 20,
  20: 10
}[scrollDelay] || 20);

class ModernAnimation extends PureComponent {
  
   constructor(props) {
       super(props); 
       this.state = {
        slideWall: 0,
      }
   };
 
  componentDidMount() {
    const {  wall } = this.props

      if (wall.isDisplay && !wall.product) {
        window.clearData = this.clearData
        const { themeId } = wall.ThemeRule;
        const prentId = document.querySelector(".waterfall-theme");
        if(prentId)  prentId.addEventListener("wheel", (e) => e.preventDefault(), {passive: false});
        setTimescroll=setTimeout(()=>{
          this.scrollDiv_init(themeId)},2000 );
      }
  }


  scrollDiv_init = (themeId) => {

    if(!this.state.slideWall){
        const elementId =  'marqueecontainer';
        DivElmnt = document.getElementById(elementId);
        if(themeId == 63) ReachedMaxScroll = false;
        (DivElmnt || {}).scrollTop = 0;
        PreviousScrollTop = 0;
        this.onScrollData();
        this.onScrollWPosts(themeId);
     }
  };

  onScrollData = () => {
    const { wall } = this.props;
    if (wall.Personalization.scrollDelay) {
      const speed = wall.ThemeRule.themeId == 66 ? 60 : speedUpPixel(wall.Personalization.scrollDelay);
      ScrollInterval = setInterval(this.scrollDiv, speed);
     }
  }


  onScrollWPosts = (themeId) => {
    const { themeHeight, wall } = this.props;
    const elementId =  'marqueecontainer';
    DivElmnt = document.getElementById(elementId);
    var dataPost = document.getElementById("wPosts");
    if (dataPost) {
      if (window.innerHeight > dataPost.clientHeight) {
          var margin = window.innerHeight;
          var min_height = dataPost.clientHeight;
          dataPost.setAttribute("style", `position: relative; margin-bottom: ${wall.isDisplay ? margin : 0}px; min-height:${min_height}px`);
      } else {
         DivElmnt.setAttribute("style", `overflow: auto; height : calc(100vh - ${themeHeight && wall.isDisplay ? themeHeight + (wall.isEditor ? 6 : 0) : 0}px)`);
      }
    }
  };

  scrollDiv = () => {
    const { themeId } = this.props.wall.ThemeRule;

    if (ScrollInterval) clearInterval(ScrollInterval);

    if (!ReachedMaxScroll) {
        (DivElmnt || {}).scrollTop = PreviousScrollTop;
        PreviousScrollTop++;
        ReachedMaxScroll = (DivElmnt || {}).scrollTop >= (DivElmnt || {}).scrollHeight - (DivElmnt || {}).offsetHeight - 300;
    } else {
      if(themeId == 63){
          ReachedMaxScroll = ReachedMaxScroll && (DivElmnt || {}).scrollTop == 0 ? false : true;
          (DivElmnt || {}).scrollTop = PreviousScrollTop;
          PreviousScrollTop--;
        }else{
         (DivElmnt || {}).scrollTop = PreviousScrollTop = 0;
         ReachedMaxScroll = false;
        }
    }
      this.onScrollData();
  };

  pauseDiv = (event) => {
    if (ScrollInterval) clearInterval(ScrollInterval);
    if (scrollTime) clearTimeout(scrollTime);
    scrollTime = setTimeout(() => this.resumeDiv(), 5000);
  };
  
  resumeDiv = () => {
    const { wall } = this.props;
    DivElmnt = document.getElementById("wPosts");
    PreviousScrollTop = (DivElmnt || {}).scrollTop;
 
    ScrollInterval = setInterval(this.scrollDiv, speedUpPixel(wall.Personalization.scrollDelay));
  };

  componentDidUpdate(prevProps, prevState) {

      const { postData, wall } = prevProps;

      if(wall.ThemeRule.themeId!=this.props.wall.ThemeRule.themeId){
          ReachedMaxScroll= false
          if(PreviousScrollTop) PreviousScrollTop=0
      } 
      
      if (postData != this.props.postData){
          this.clearTime(1)
          this.scrollDiv_init(wall.ThemeRule.themeId);
      }
   }


   clearData=()=> { this.clearTime() }
    
   clearTime=(status=0)=>{
       if(this.props.wall.isDisplay) this.setState({ slideWall : !status ? 1 : 0 })
       if (ScrollInterval) clearInterval(ScrollInterval);
       if (ScrollInterval) ScrollInterval=0;
       if (scrollTime) clearTimeout(scrollTime);
       if(setTimescroll) clearTimeout(setTimescroll)
       if(ReachedMaxScroll) ReachedMaxScroll=false
       if(PreviousScrollTop) PreviousScrollTop=0
   } 


  componentWillUnmount() {

    const prentId = document.querySelector(".waterfall-theme");
    if(prentId)  prentId.removeEventListener("mousewheel", (e) => e.preventDefault(), false);
    
    this.clearTime()
  }

  render() {
    return <> 
    { ({...this.props.children}) }</>
  }
}
export default memo(ModernAnimation);