import React, { PureComponent } from "react";

const ThemeRender = ({ network }) => {
    const networkStyles = {
        3: { className: 'tb_rating_ico__ tb__icon tb-rating', color: '#1b74e4' },
        4: { className: 'tb_rating_ico__ tb__icon tb-rating tb-google-rating', color: '#F8B90C' },
        19: { className: 'tb_rating_ico__ tb__icon tb-rating tb-yelp-rating', color: '#e00707' },
        23: { className: 'tb_rating_ico__ tb__icon tb-rating tb-airbnb-rating', color: '#ff385c' },
        29: { className: 'tb_rating_ico__ tb__icon tb-rating', color: '#613983' },
    };

    const { className, color } = networkStyles[network.id] || {
        className: 'tb_rating_ico__ tb__icon tb-rating',
        color: '#F8B90C',
    };

    return <div className={className} style={{ color }} tb-network={network.id}></div>;
};
export default class Rating extends PureComponent {
    render() {
        const { rating, network } = this.props;

        return (
            <div    className="tb__rating__" aria-hidden>
                {
                    rating ? [...Array(rating)].map((el, index) => 
                      <ThemeRender key={index} network={network} />
                     ) : null
                }
            </div>
        );
    }
}
